@media screen and (min-width: 992px) {
    #app {
        main {
            .jumbotron .jumbotron-content {
                .jumbotron-description {
                    width: 70%;
                }

                .jumbotron-description {
                    font-size: 1.5rem;
                }
            }

            .restaurant-list .card {
                flex-basis: 20%;
                max-width: 260px;
            }

            .detail-restaurant {
                max-width: 992px !important;
                margin: auto;
            }
        }
    }
}